import { Notify } from 'notiflix/build/notiflix-notify-aio';
const button = document.querySelector('.form-submit');
const form = document.querySelector('form');
form.addEventListener('submit', onSubmitForm);

function createPromise(position, delay) {
 const promise = new Promise((resolve,reject) => {
   const shouldResolve = Math.random() > 0.3;
   if (shouldResolve) {
     resolve({position,delay});
   } else {
     reject({ position, delay });
   }
 })
  return promise;
}

function onSubmitForm(e) {
  e.preventDefault();
  const formData = new FormData(form);
  let data = {
    step:Number(formData.get('step')),
    delay:Number(formData.get('delay')),
    amount:Number(formData.get('amount')),
    position:0
  }
  button.disabled = true;
  setTimeout(()=>{
  let timeout = setInterval(()=>{

    data.position++;
    createPromise(data.position,data.delay).then(({position,delay}) => Notify.success(`Fulfilled promise ${position} in ${delay}ms` ))
      .catch(({position,delay}) => Notify.failure(`Rejected promise ${position} in ${delay}ms`));
    data.delay += data.step
    if (data.position >= data.amount) {
      clearInterval(timeout);
      button.disabled = !button.disabled;
    }
  },data.step)
  },data.delay)
};

